@import url(https://fonts.googleapis.com/css?family=Alegreya+Sans:100i,100, 300i|Josefin+Sans:100&display=swap);
body {
  background-color: black;
  color: white;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  position: absolute;
  height: 100%;
  width: 100%;
  text-align: center;
  font-family: 'Alegreya Sans', sans-serif; }

.previewContainer {
  display: flex;
  justify-content: flex-start;
  height: calc(100% - 70px);
  overflow-x: scroll;
  overflow-y: hidden;
  scrollbar-width: none;
  -ms-overflow-style: none; }
  .previewContainer::-webkit-scrollbar {
    width: 0 !important; }
  .previewContainer .imagePreviewContainer {
    height: 100%;
    width: 400px;
    position: relative;
    cursor: pointer;
    opacity: 0.5; }
    .previewContainer .imagePreviewContainer:hover {
      opacity: 1; }
    .previewContainer .imagePreviewContainer:hover .previewBackground {
      opacity: 0.6; }
    .previewContainer .imagePreviewContainer:hover .descriptionContainer {
      opacity: 1; }
    .previewContainer .imagePreviewContainer .previewBackground {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 200px;
      background-color: black;
      opacity: 0; }
    .previewContainer .imagePreviewContainer .descriptionContainer {
      position: absolute;
      bottom: 0;
      height: 200px;
      padding: 50px 20px;
      box-sizing: border-box;
      color: white;
      text-align: left;
      opacity: 0; }
      .previewContainer .imagePreviewContainer .descriptionContainer .itemTitle {
        font-family: 'Alegreya Sans', sans-serif;
        text-transform: uppercase;
        font-size: 2rem;
        color: white;
        font-style: italic;
        font-weight: 300; }
      .previewContainer .imagePreviewContainer .descriptionContainer .itemDate {
        position: absolute;
        bottom: 15px; }
    .previewContainer .imagePreviewContainer .imagePreview {
      height: 100%;
      max-width: 400px;
      object-fit: cover;
      margin-right: 10px; }

@media (max-width: 480px) {
  .imagePreviewContainer .imagePreview {
    max-width: 70vw !important; } }

.loadingScreenContainer {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center; }
  .loadingScreenContainer .loadingText {
    font-family: 'Alegreya Sans', sans-serif;
    font-weight: 100;
    letter-spacing: 35px;
    font-size: 5rem;
    text-transform: uppercase;
    opacity: 1;
    -webkit-transition: opacity 1500ms ease-out;
    transition: opacity 1500ms ease-out; }
  .loadingScreenContainer .hidden {
    opacity: 0.2; }

@media (max-width: 480px) {
  .loadingScreenContainer .loadingText {
    font-size: 1rem; } }

.imageFullscreenContainer {
  position: fixed;
  top: 0;
  width: 100%;
  height: calc(100% - 30px);
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center; }
  .imageFullscreenContainer .fullscreenImage {
    max-width: 100%;
    max-height: 100%;
    z-index: 12;
    object-fit: contain; }
  .imageFullscreenContainer .toggleFullscreen {
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 13;
    cursor: pointer; }
  .imageFullscreenContainer .closeButton {
    width: 25px;
    height: 25px; }
  .imageFullscreenContainer .fullscreenBackground {
    z-index: 12;
    background-color: black;
    opacity: 0.9;
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%; }

.toolbar {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: black;
  z-index: 50;
  height: 70px;
  display: flex;
  justify-content: space-around;
  padding: 15px 0;
  border-top: solid 1px white;
  box-sizing: border-box; }
  .toolbar > div {
    cursor: pointer; }
  .toolbar .toolbarIcon {
    width: 50px;
    height: 50px; }

.linksHome {
  text-align: center;
  margin-top: 50px; }

.linkContainer {
  width: 600px;
  border-bottom: solid 1px grey;
  padding: 10px 0;
  text-align: left;
  font-size: 1.2rem;
  margin: 10px auto;
  color: grey; }
  .linkContainer .linkText {
    text-decoration: none;
    color: inherit; }
  .linkContainer .actionButton {
    cursor: pointer;
    float: right;
    margin-right: 20px;
    width: 20px;
    height: 20px; }

.loginButton {
  position: fixed;
  right: 20px;
  top: 20px;
  font-size: 1.5rem;
  background-color: black;
  color: white;
  outline: none;
  border: solid 1px white;
  border-radius: 2px;
  padding: 5px 10px; }

.loginInfo {
  margin: 100px 20%;
  font-size: 1.3rem;
  font-weight: 300; }
  .loginInfo p {
    text-align: left; }
  .loginInfo a {
    color: skyblue;
    text-decoration: none; }

.imgurForm {
  margin: 20px 30px; }
  .imgurForm input {
    color: white;
    background-color: inherit;
    border: 0;
    outline: none;
    border-bottom: solid 1px grey;
    padding-bottom: 10px;
    margin-right: 20px;
    width: 300px; }
  .imgurForm button {
    color: white;
    background-color: black;
    padding: 5px 10px;
    border: 1px solid white; }

@media (max-width: 480px) {
  .imgurForm {
    margin-top: 150px;
    width: 90%; }
    .imgurForm input {
      width: 60%; }
  .linkContainer {
    width: 90%; } }

